import CaxView, { Model } from "./CAXViewBasic"
import styles from "./App.module.css"
import { useEffect, useState } from "react"

const getUint8ArrayFromBlob: (blob: Blob) => Promise<Uint8Array> = (blob) => {
  return new Promise<Uint8Array>((res, rej) => {
    const reader = new FileReader()
    const onLoad = (e: ProgressEvent<FileReader>) => {
      if (typeof e.target?.result === "object" && e.target.result) {
        res(new Uint8Array(e.target.result))
      } else {
        rej("Cannot get an uint8Array from blob")
      }

      reader.removeEventListener("load", onLoad)
    }
    reader.addEventListener("load", onLoad)
    reader.readAsArrayBuffer(blob)
  })
}

function App() {
  const [model, setModel] = useState<Model>(null)

  const modelUrl = new URL(window.location.href).searchParams.get("model")

  useEffect(() => {
    const fetchModel = async () => {
      if (modelUrl === null) {
        setModel(null)
      } else {
        const res = await fetch(modelUrl)
        const blob = await res.blob()
        setModel(await getUint8ArrayFromBlob(blob))
      }
    }

    fetchModel()
  }, [modelUrl])

  return <CaxView className={styles.caxview} model={model} />
}

export default App
